@import './icofont';
a.tf-amz-image-block-wrapper {
    display: block;
}

.tf-amz-image-wrapper {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
    &.has-border {
        border: 3px solid #231f20;
        padding: 5px;
    }
    .tf-featured-media {
        text-align: center;
        img {
            width: 100%!important;
            vertical-align: bottom;
        }
    }
}

.wp-block-tf-block-i2-azone-image {
    &.alignright,
    &.alignleft {
        max-width: 45%;
    }
    &.alignright {
        margin-left: 10px;
    }
    &.alignleft {
        margin-right: 10px;
    }
}

.tf-featured-image {
    p.has-background {
        padding: 10px;
    }
}

.tf-i2-content {
    .tf-btn-wrapper,
    a.tf-btn-wrapper {
        display: inline-flex;
        background-color: #f0c14b;
        color: #111;
        padding: 6px 12px 6px 12px;
        align-content: center;
        font-size: 1.2rem;
        justify-content: center;
        text-decoration: none!important;
        transition: all .4s ease-in-out;
        float: right;
        &:active,
        &:visited {
            color: #111;
        }
        >i {
            margin-right: 10px;
            align-self: center;
        }
        &.tf-size-small {
            font-size: .8rem;
            padding: 5px 10px;
            >i {
                margin-right: 5px;
            }
        }
        &.tf-size-medium {
            font-size: 1.4rem;
            padding: 10px 20px;
            >i {
                margin-right: 10px;
            }
        }
        &.tf-size-big {
            font-size: 2rem;
            padding: 15px 25px;
            >i {
                margin-right: 15px;
            }
        }
        &.tf-size-larg {
            font-size: 3rem;
            padding: 20px 40px;
            >i {
                margin-right: 20px;
            }
        }
        &:hover {
            opacity: .8;
        }
    }
}

.tf-i2 {
    box-sizing: border-box;
}

//i2-product-box css
.wp-block-themesfirst-i2-azon-product-box {
    clear: both;
}

.tf-i2-product {
    display: flex;
    max-width: 100%;
    margin-bottom: 15px;
    padding: 20px;
    background: #fff;
    font-size: 15px;
    line-height: 1.6;
    white-space: normal;
    .tf-i2-image {
        width: 35%;
        position: relative;
        img {
            max-width: 100%;
            display: block;
        }
    }
    .tf-i2-content {
        width: 65%;
        padding-left: 15px;
        .tf-i2-title {
            display: block;
            margin: 0 0 15px 0;
            font-size: 18px;
            font-weight: 700;
        }
        .tf-i2-features {
            margin: 0 0 5px;
            padding: 0 0 10px 20px;
            list-style-type: disc;
            list-style-position: outside!important;
            li {
                margin: 0;
                padding: 0;
                margin-bottom: 8px;
                background: none;
                word-wrap: break-word;
            }
        }
    }
}

.tf-i2-horizontal {
    flex-direction: column;
    .tf-i2-image,
    .tf-i2-content {
        width: initial
    }
    .tf-i2-image img {
        margin: 0 auto 15px;
        width: auto;
    }
}

